.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header__body {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
}

.header__logo {
  flex-shrink: 0;
  display: flex;
}

.header__img {
  vertical-align: top;
  width: 100%;
