  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__picture {
  margin-bottom: 40px;
  padding: 32px 19px 0;
}

.card__pic {
  aspect-ratio: 160 / 134;
  object-fit: cover;
  width: 100%;
  display: block;
}

.card__content {
  padding: 0 16px 16px;
}

.card__code {
  color: #616070;
  letter-spacing: 0%;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
  display: flex;
}

.card__price {
  box-sizing: border-box;
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 150%;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.card__btn:focus, .card__btn:active {
  text-decoration: none;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  text-decoration: none;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
