}

.main__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
}

.main__body {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main__body {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main__body {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main__body {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
